@charset "UTF-8";
* {
  font-family: "方正启体简体", "微软雅黑", "黑体";
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

.container,
.containerFull {
  max-width: 640px;
  min-height: 150px;
  position: relative;
  overflow-x: hidden;
  table-layout: fixed;
  word-break: break-all;
  margin: 0 auto;
}

.container {
  padding-bottom: 48px;
  padding-top: 44px;
}

header,
.header {
  width: 100%;
  height: 44px;
  border-bottom: 1px solid #e1e1e1;
}
header .brand,
.header .brand {
  width: 20%;
  font-size: 30px;
  margin-left: 10px;
  float: left;
}
header .search,
.header .search {
  float: right;
  height: 30px;
  margin-right: 10px;
  width: 60%;
}
header .search .fa,
.header .search .fa {
  position: absolute;
  margin-top: 10px;
  margin-left: 5px;
  z-index: 99;
}
header .search input,
.header .search input {
  margin-top: 2px;
  width: 100%;
  font-size: 20px;
  border: 1px solid #333;
  padding: 4px 0 4px 20px;
  box-sizing: border-box;
  background: transparent;
}

.top {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background: #05a6b1;
  color: #fff;
  text-align: center;
  z-index: 9999;
  border: 0;
}
.top .back {
  position: absolute;
  left: 10px;
  top: 50%;
  margin-top: -12px;
  font-size: 25px;
  color: #fff;
}
.top .btn {
  position: absolute;
  right: 10px;
  top: 50%;
  margin-top: -17px;
  border: 1px solid #fff;
  padding: 5px;
  color: #fff;
}
.top .title {
  line-height: 44px;
}

footer,
.footer {
  width: 100%;
  height: 48px;
  background: #fff;
  border: 1px solid #e1e1e1;
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 9999;
}
footer a,
footer .item,
.footer a,
.footer .item {
  width: 20%;
  text-align: center;
  display: block;
  float: left;
  padding-top: 4px;
  box-sizing: border-box;
  position: relative;
}
footer a .tip,
footer .item .tip,
.footer a .tip,
.footer .item .tip {
  display: block;
  position: absolute;
  right: 30px;
  top: 0px;
  width: 16px;
  height: 16px;
  color: #fff;
  background: #e1454b;
  font-size: 10px;
}
footer a.active,
footer .item.active,
.footer a.active,
.footer .item.active {
  color: #4693d0;
}
footer a .fa,
footer .item .fa,
.footer a .fa,
.footer .item .fa {
  font-size: 30px;
  display: block;
}
footer.comment,
.footer.comment {
  background: rgba(23, 12, 23, 0.7);
}
footer.comment a,
.footer.comment a {
  width: 100%;
  text-align: center;
}

.userHeader {
  position: relative;
  padding-top: 25px;
  padding-bottom: 30px;
  background: #00E0F2;
}
.userHeader .avatar {
  width: 90px;
  height: 90px;
  margin: 0px auto;
  text-align: center;
  line-height: 0;
  position: relative;
}
.userHeader .avatar img {
  width: 100%;
  height: 100%;
  border-radius: 100%;
}
.userHeader .avatar::after {
  content: "";
  width: 100px;
  height: 100px;
  border: 2px solid #99e3ff;
  border-radius: 100%;
  position: absolute;
  left: -7px;
  top: -7px;
  z-index: 0;
}
.userHeader .name {
  text-align: center;
  padding-top: 10px;
}

.group {
  position: relative;
}
.group .fa {
  font-size: 20px;
  position: absolute;
  left: 10px;
  top: 10px;
}
.group input {
  width: 100%;
  padding: 5px 0 5px 30px;
  box-sizing: border-box;
  font-size: 20px;
  border: 1px solid #c1c1c1;
}
.group input:required {
  background: #fff url(../image/required.png) no-repeat 98% center;
  background-size: 8px;
}
.group input:focus {
  border: 0;
}
.group input:focus:invalid {
  background: #fff url(../image/invalid.png) no-repeat 98% center;
  background-size: 8px;
}
.group input:focus + .fa {
  color: #e1454b;
}
.group input:focus + .fa + .tip {
  display: block;
}
.group input:valid {
  background: #fff url(../image/valid.png) no-repeat 98% center;
  background-size: 8px;
}
.group input:valid + .fa {
  color: #5cb85c;
}
.group .tip {
  display: none;
  margin-left: 40px;
  color: #e1454b;
}
.group.error .fa {
  color: #f00;
}
.group.error input {
  border: 2px solid #f00;
}
.group.code img {
  height: 36px;
  padding: 1px;
  position: absolute;
  width: 100px;
  right: 0;
  top: 0;
  border-left: 2px solid #c1c1c1;
}
.group.code.error img {
  border-left: 2px solid #f00;
}
.group.code input {
  padding-right: 100px;
}

.checkbox {
  width: 40px;
  height: 20px;
  border: 1px solid #767676;
  background: #fff;
  position: relative;
}
.checkbox input[type=checkbox] {
  visibility: hidden;
}
.checkbox input[type=checkbox]:checked + label {
  left: 20px;
  background: #05a6b1;
}
.checkbox label {
  display: block;
  width: 20px;
  height: 20px;
  -webkit-transition: all 0.5s ease;
  -moz-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  -ms-transition: all 0.5s ease;
  transition: all 0.5s ease;
  cursor: pointer;
  position: absolute;
  top: 0;
  left: 0;
  background: #000;
}

.checkboxGroup {
  margin: 5px;
  font-size: 20px;
}
.checkboxGroup .checkbox {
  float: left;
}

.login {
  margin-top: 30px;
}
.login .btn {
  margin-top: 10px;
  width: 100%;
  background: #05a6b1;
  color: #fff;
  line-height: 40px;
  border: none;
}

.textRight {
  text-align: right;
}

.textCenter {
  text-align: center;
}

.oauth {
  position: relative;
  margin-top: 20px;
}
.oauth .textCenter::before,
.oauth .textCenter::after {
  content: "";
  width: 40%;
  height: 1px;
  background-color: #e2e2e2;
  position: absolute;
  top: 10px;
}
.oauth .textCenter::before {
  left: 0;
}
.oauth .textCenter::after {
  right: 0;
}

.carousel {
  position: relative;
}
.carousel ul {
  position: relative;
  width: 2000px;
  zoom: 1;
  left: 0;
  /*-webkit-transition: left 1s;
  -moz-transition: left 1s;
  transition: left 1s;*/
}
.carousel ul li {
  list-style: none;
  float: left;
  width: 640px;
}
.carousel ul li img {
  width: 100%;
  display: block;
}
.carousel .link {
  position: absolute;
  top: 10px;
  left: 50%;
  margin-left: -20px;
}
.carousel .link span {
  float: left;
  margin-right: 8px;
  width: 8px;
  height: 8px;
  display: block;
  background: rgba(255, 255, 255, 0.4);
  cursor: pointer;
  text-indent: 9999px;
  -webkit-border-radius: 20px;
  -moz-border-radius: 20px;
  -o-border-radius: 20px;
  border-radius: 20px;
}
.carousel .link span.active {
  background: #fff;
}

.clear {
  clear: both;
}

.panel > .title {
  border: 1px solid #767676;
  height: 40px;
  line-height: 40px;
}
.panel > .title span {
  font-size: 25px;
  margin-left: 10px;
}
.panel > .title a {
  float: right;
  border-left: 1px solid #c1c1c1;
}
.panel .body {
  border: 1px solid #c1c1c1;
  border-top: 0;
}

.grid .gridItem {
  width: 49%;
  text-align: center;
  float: left;
}
.grid .gridItem:nth-child(odd) {
  width: 49%;
  margin-right: 2%;
}
.grid .gridItem img {
  width: 100%;
}

.list {
  clear: both;
}
.list .listItem,
.list .listItemImage {
  width: 100%;
  padding: 5px 5px 20px 5px;
  border-bottom: 1px dotted #c1c1c1;
}
.list .listItem.active,
.list .listItemImage.active {
  background: #99e3ff;
  border-left: 5px solid #00E0F2;
}
.list .listItem:first-child {
  border-top: 1px dotted #c1c1c1;
}
.list .listItem:last-child {
  padding-top: 0;
}
.list .listItemImage {
  position: relative;
}
.list .listItemImage img {
  width: 30%;
  max-width: 100px;
  float: left;
  position: relative;
}
.list .listItemImage .content {
  position: relative;
  float: left;
}

.form button {
  height: 50px;
  border: 0;
  width: 7%;
  font-size: 40px;
}
.form button:first-child {
  float: left;
}
.form button:last-child {
  float: right;
}
.form textarea {
  height: 50px;
  width: 86%;
  box-sizing: border-box;
}

.chat .left,
.chat .right {
  width: 100%;
  display: table;
  table-layout: fixed;
  margin-bottom: 10px;
}
.chat .left > dd,
.chat .right > dd {
  padding: 0 10px;
  display: table-cell;
  vertical-align: top;
}
.chat .left .content,
.chat .right .content {
  border: 1px #e3e3e3 solid;
  padding: 10px;
  display: inline-block;
  position: relative;
}
.chat .time {
  text-align: center;
  margin-bottom: 10px;
}
.chat .time span {
  display: inline-block;
  padding: 2px 6px;
  background-color: #cbcbd0;
}
.chat .avatar {
  width: 35px;
  display: table-cell;
  vertical-align: top;
}
.chat .avatar img {
  width: 35px;
  height: 35px;
}
.chat .left .content {
  background-color: #fff;
}
.chat .left .content::before {
  content: "";
  display: block;
  width: 0;
  height: 0;
  border-top: 5px solid transparent;
  border-right: 7px solid #e3e3e3;
  border-bottom: 5px solid transparent;
  position: absolute;
  top: 10px;
  left: -8px;
  z-index: 1;
}
.chat .left .content::after {
  content: "";
  display: block;
  width: 0;
  height: 0;
  border-top: 5px solid transparent;
  border-right: 7px solid #fff;
  border-bottom: 5px solid transparent;
  position: absolute;
  top: 10px;
  left: -6px;
  z-index: 1;
}
.chat .right > dd {
  text-align: right;
}
.chat .right .content {
  background-color: #a3e990;
}
.chat .right .content::before {
  content: "";
  display: block;
  width: 0;
  height: 0;
  border-top: 5px solid transparent;
  border-left: 7px solid #e3e3e3;
  border-bottom: 5px solid transparent;
  position: absolute;
  top: 10px;
  right: -8px;
  z-index: 1;
}
.chat .right .content::after {
  content: "";
  display: block;
  width: 0;
  height: 0;
  border-top: 5px solid transparent;
  border-left: 7px solid #a3e990;
  border-bottom: 5px solid transparent;
  position: absolute;
  top: 10px;
  right: -6px;
  z-index: 1;
}

.menu .item {
  border-right: 1px dotted #4693d0;
}
.menu .item:nth-child(5n) {
  border: 0;
}

.menuLarge .item {
  width: 50%;
  border-right: 1px dotted #4693d0;
  border-top: 1px dotted #4693d0;
  height: 120px;
}
.menuLarge .item .fa {
  font-size: 50px;
  margin-top: 20px;
}
.menuLarge .item:nth-child(even) {
  border-right: 0;
}
.menuLarge .item:first-child, .menuLarge .item:nth-child(2) {
  border-top: 0;
}

.menuList a {
  display: block;
  padding: 10px;
  background: #99e3ff;
  margin-top: 2px;
}
.menuList a .fa:nth-child(2) {
  float: right;
  margin-right: 10px;
}

.expand {
  margin: 2px 0;
}
.expand .title {
  background: #c1c1c1;
  padding: 10px;
}
.expand .title .fa {
  float: right;
}
.expand .content {
  display: none;
  border: 1px solid #c1c1c1;
}
.expand.active .title .fa {
  transform: rotate(180deg);
  -ms-transform: rotate(180deg); /* IE 9 */
  -webkit-transform: rotate(180deg); /* Safari and Chrome */
  -o-transform: rotate(180deg); /* Opera */
  -moz-transform: rotate(180deg); /* Firefox */
}
.expand.active .content {
  display: block;
}

.tab .tab-header {
  width: 100%;
  height: 44px;
  line-height: 45px;
  border-bottom: 1px solid #cccccc;
  box-sizing: border-box;
}
.tab .tab-header > li {
  width: 33%;
  float: left;
  height: 45px;
  box-sizing: border-box;
  text-align: center;
  list-style: none;
}
.tab .tab-header > li:nth-child(2) {
  width: 34%;
}
.tab .tab-header > li.active {
  border-bottom: 3px solid #05a6b1;
  height: 43px;
}
.tab .tab-content > li {
  display: none;
  width: 100%;
}
.tab .tab-content > li.active {
  display: block;
}

.message li {
  padding: 8px 0;
  list-style: none;
  position: relative;
  border-bottom: 1px solid #c1c1c1;
  height: 80px;
}
.message li .time {
  position: absolute;
  top: 10px;
  right: 10px;
  color: #767676;
}
.message .content {
  font-size: 20px;
}
.message .content > p {
  color: #767676;
  margin-top: 10px;
}
.message .avatar {
  float: left;
  position: relative;
  width: 80px;
  margin-right: 10px;
}
.message .avatar span {
  position: absolute;
  right: -4px;
  top: -4px;
  background: #f00;
  color: #fff;
  padding: 2px 5px;
  display: block;
}

.friends .tag,
.friends .friend {
  display: block;
}
.friends .tag {
  background: #c1c1c1;
  padding: 3px 10px;
}
.friends .friend {
  height: 60px;
  font-size: 30px;
  padding: 14px 0;
  border-bottom: 1px solid #c1c1c1;
  box-sizing: border-box;
}
.friends .friend img {
  width: 60px;
  height: 60px;
  float: left;
  margin: -14px 10px -14px 0;
}

.blog li,
.comments li {
  position: relative;
  padding: 0.4rem 0.5rem 0;
  overflow: hidden;
  line-height: initial;
  border-bottom: 10px solid #f1f1f1;
}
.blog .title .time,
.comments .title .time {
  float: right;
}
.blog .title img,
.comments .title img {
  width: 40px;
}

.blog .content .commentCount,
.blog .content .viewCount {
  float: right;
}

.commentText {
  margin-left: 3.3rem;
}
.commentText .commentSecond {
  border: 1px solid #f1f1f1;
  font-size: 0.75rem;
}

.commentText .comments .user {
  color: #60abff;
  font-size: 1.2rem;
  font-weight: 600;
}

#content .bottom {
  margin-top: 5px;
}
#content .bottom > a {
  float: right;
}

.user {
  height: 50px;
}
.user img {
  float: left;
  width: 50px;
  margin-right: 10px;
}

/** Metro **/
.metro {
  position: relative;
}
.metro .metroItem {
  float: left;
  background: rgba(0, 255, 255, 0.4);
}

/** 手风琴 **/
.accordion {
  height: 200px;
  width: 100%;
}
.accordion li {
  list-style: none;
}
.accordion .accordionItem {
  float: left;
  height: 200px;
  width: 30px;
  -webkit-transition: width 1s;
  -moz-transition: width 1s;
  transition: width 1s;
}
.accordion .accordionItem.active .content {
  display: block;
}
.accordion .accordionItem .title {
  width: 30px;
  padding: 5px;
  box-sizing: border-box;
  border: 1px solid #00E0F2;
  height: 200px;
  float: left;
}
.accordion .accordionItem .content {
  display: none;
  height: 200px;
  width: 300px;
  float: left;
}
.accordion .accordionItem .content img {
  height: 150px;
}

/** Dialog **/
.tipDialog,
.notifyDialog {
  position: fixed;
  background: rgba(0, 255, 255, 0.4);
  padding: 10px 20px;
  -webkit-transition: all 1s;
  -moz-transition: all 1s;
  transition: all 1s;
  z-index: 9999;
  box-sizing: border-box;
}

.tipDialog {
  bottom: 80px;
  display: none;
}

.notifyDialog {
  position: fixed;
  top: 10px;
  right: 0;
  width: 0px;
}

@media screen and (max-width: 540px) {
  header .brand,
  .header .brand {
    width: 30%;
    font-size: 20px;
    margin-left: 0;
    margin-top: 10px;
  }
  header .search,
  .header .search {
    width: 69%;
    margin-right: 0;
  }
  .header .form button {
    font-size: 20px;
  }
}